import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { HttpClient } from '@angular/common/http';


@Component({
  selector: 'app-sentiment-form',
  templateUrl: './sentiment-form.component.html',
  styleUrls: ['./sentiment-form.component.css']
})
export class SentimentFormComponent implements OnInit {
  form: FormGroup;
  responseData: any;  

  constructor(
    public fb: FormBuilder,
    private http: HttpClient
    ) { 
    this.form = this.fb.group({
      inputText: ['']
    })
  }

  ngOnInit(){
  }

  
  submitForm() {
    var formData: any = new FormData();
    formData.append("inputText", this.form.get('inputText').value)
  
    this.http.post('http://127.0.0.1:5000/predictSentiment', formData, {responseType: 'text' as 'json'}).subscribe(
      
      (response) => { this.responseData = response },
      (error) => { console.log(error) }
    );
  }

}

